.app-product {
	border: 1px solid rgba(30,18,18,0.3);
	width: auto;
	margin-bottom: 10%;
	transition: transform .2s; 
	font-family: 'Montserrat', sans-serif;
}
.app-product:hover {
	transform: scale(1.07);
	
}
.app-product-flag-discount {
    border-radius: 6px 6px 6px 6px;
    color: #fff;
    display: block;
    float: left;
    margin-top: 5%;
    padding: 10px 20px;
    background: #AF4C4C;
    font-size: 20px;
    font-weight: 400;
    left: 5px;
    position: absolute;
}
.grid-link_org_price {
    font-size: 20px;
    line-height: normal;
    font-weight: normal;
    color: red;
    margin: 0 0 0px;

}
.product-detail {
    text-align: center;
    float: left;
    width: 100%;
    position: relative;
    padding: 20px 0px 20px;
    z-index: 1;
    border-radius: 0 0 0px 0px;

}
.product_title {
    font-family: Abel;
    text-transform: capitalize;
    letter-spacing: 1px;
    font-weight: normal;
    font-size: 18px;
    color: #000000;
    display: inline-block;
    width: 100%;
    margin-top: 10%;
    line-height: normal;
    -webkit-transition: top 1s ease-out 0.5s;
     -moz-transition: top 1s ease-out 0.5s;
     -o-transition: top 1s ease-out 0.5s;
     transition: top 1s ease-out 0.5s;
}


.product_title:hover, .product_title:focus {
    color: #00e7e4;
    text-decoration: none;

}
.product_title {
    position: relative;
    text-align: center;
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #e4e4e4;
}
.icon-cart {
	color: turquoise;
	font-size: 20px;
	border: 1px solid #fafafa;
	width: 25%;
	background-color: #fafafa;
	text-align: center;
	border-radius: 5%;
}
.icon-cart:hover {
	color: turquoise;
	
}

.product_title:after {
    clear: both;
    content: "";
    display: inline-block;
    height: 10px;
    margin: 0 auto;
    position: absolute;
    bottom: -5px;
    left: 0;
    right: 0;
    text-align: center;
    width: 10px;
    border-radius: 1px;
    -webkit-border-radius: 1px;
    -moz-border-radius: 1px;
    -ms-border-radius: 1px;
    -o-border-radius: 1px;
    box-shadow: 0 0 0 8px #ffffff;
    -webkit-box-shadow: 0 0 0 8px #ffffff;
    border-radius: 1px;
    background: #00e7e4;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
}