@import url('https://fonts.googleapis.com/css2?family=Prociono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mate&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');


.detail-way {
	padding-bottom: 10px;
	border-bottom: 1px solid  #AF4C4C;
}
.detail-way > a {
	font-size: 15px;
	font-family: 'Montserrat', sans-serif;
	text-decoration: none;
	color: #333333;
}

.detail-way > a:hover {
	color: #666666;
}


.detail-rating {
	cursor: pointer;
	font-size: 30px;
}

.fa-star{
	transition: transform .2s; 
	cursor: pointer;
}

.fa-star:hover {
	transform: scale(0.8);
}

.detail {
	display: flex;
	flex-wrap: wrap;
	padding-top: 20px;
}

.detail > .detail-image {
	width: 50%;
	padding: 0 20px 0 20px;
}
.detail > .detail-info {
	width: 50%;
}

.detail > .detail-info >  .detail-rating >  .checked-star {
	color: orange;
}

.detail > .detail-info > .btn {
	transition: transform .2s; 
	background-color: #AF4C4C;
	color: #fff;
	border-radius: 0;
}

.detail > .detail-info > .btn:hover {
	transform: scale(0.95);
}

.detail > .detail-info > .detail-title{
	font-size: 30px;
}

.detail > .detail-info > h1{
	font-size: 20px;
	margin-top: 30px;
}

.detail-char-header {
	text-align: center;
	margin-bottom: 30px;
}

.detail-char-table {
	font-size: 100%;
	margin: 0 auto;
}

.detail-char-table > tbody > tr > th {
	padding-right: 5rem !important;
}


.detail > .detail-info > div > .detail-price{
	color: #990000;
}


.detail-description h1{
	font-size: 150%;
}


@media only screen and (max-width: 768px) {

	.detail > .detail-image {
		width: 100%;
	}
	.detail > .detail-info {
		width: 100%;
		padding-top: 20px;
		padding-right: 10%;
		padding-left: 10%;
	}

	.detail > .detail-info > .detail-title{
		font-size: 15px;
	}

	.detail-way > a {
		font-size: 7px;
	}

	.detail > .detail-info > .btn{
		width: 50%;
		font-size: 10px;
	}

	.detail > .detail-info > h1{
		font-size: 10px;
	}
	.detail > .detail-info > p{
		font-size: 6px;
	}

	.detail-rating {
		font-size: 20px;
	}
	.detail > .detail-info > div > .detail-price{
		font-size: 20px;	
	}

	.detail > .detail-info > div > .not-detail-price{
		font-size: 10px;
	}

	.detail-char-header {
		margin-bottom: 20px;
	}

	.detail-char-table {
		font-size: 80%;
	}

	.detail-char-table > tbody > tr > th {
		padding-right: 1rem !important;
	}


	.detail-description h1{
		font-size: 100%;
	}
}
.app-detail-feedback {
	width: 100%;
	
	background-color: #AF4C4C;
	margin-bottom: 5%;
	margin-top: 5%;

}
.app-detail-feedback-count {
	width: 50%;
	text-align: center;
	color: white;
	border-right: 1px solid white;
	font-family: 'Montserrat';
	font-size: 100%;

}
.app-detail-feedback-write {
	width: 50%;
	text-align: center;
	background-color: white;
	color: #AF4C4C;
	border: 1px solid #AF4C4C;
	
}
.app-detail-btn-modal {
  background-color: #AF4C4C;
  color: white;
}
.app-detail-btn-modal:hover {
  color: white;
}
.app-detail-modal-title {
  font-size: 30px;
  font-family: Montserrat;
  color: #AF4C4C;
  text-align: center;
}
.app-detail-modal-info {
  font-size: 18px;
  font-family: Roboto;
  color: #444444;
  text-align: center;
}
.app-detail-feedback-write{
  display: block;
  font-size: 18px;
  font-family: sans-serif;
  cursor: pointer;
  color: #333;
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all .35s;
}

.app-detail-feedback-write div span{
  position: relative;
  z-index: 2;
}

.app-detail-feedback-write:after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #AF4C4C;
  transition: all .35s;
}
.app-detail-list-image {
	width: 100% !important;
}
.im{ 
	width: 100% !important;
}

.app-detail-feedback-write:hover{
  color: #fff;
}


.app-detail-feedback-write:hover:after{
  width: 100%;
}