#to_cart {
	position: fixed;
	transition: all 0.3s ease-in-out 0s;
	font-size: 40px;
	width: 80px;
	height: 80px;
	margin: 0 auto;
	text-align: center;
	border-radius: 50px;
	padding-top: 9px;
	margin-bottom: 50px;

	background-color: rgba(175, 76, 76, 0.3);
}
#to_cart a {
	color: #AF4C4C !important;
	width: 80px;
	height: 80px;
	border-radius: 50px;
	transition: all 0.3s ease-in-out 0s;
}




#to_cart::before, #to_cart a::before {
	border-radius: 1000px;
	min-width: calc(300px + 12px);
	min-height: calc(60px + 12px);
	border: 6px solid #AF4C4C ;
	box-shadow: 0 0 60px rgba(0,255,203,.64);
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	opacity: 0;
	transition: all .3s ease-in-out 0s;
}

#to_cart::after, #to_cart a::after  {
	content: '';
	width: 30px; height: 30px;
	border-radius: 100%;
	border: 6px solid #AF4C4C ;
	position: absolute;
	z-index: -1;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	animation: ring 1.5s infinite;
}

#to_cart a::after {
	animation: ring 1s infinite;
}

@keyframes ring {
	0% {
	  width: 80px;
	  height: 80px;
	  opacity: 0.5;
	}
	100% {
	  width: 250px;
	  height: 250px;
	  opacity: 0;
	}
  }
