.footer {
	width: 100%;
	background-color: #333333;

	display: flex;
	flex-wrap: wrap;
	padding: 5vh 10% 10vh 10%;


	font-family: 'Mate', serif;
}

.footer > .footer-item {
	width: 33%;
	text-align: center;
	padding: 0;

}

.footer > .footer-item > li {
	margin-bottom: 10px;
}

.footer > .footer-item > li > .footer-link{
	color: #fff;
	text-decoration: none;
}

.footer > .footer-item > li > .footer-link:hover, .social-networks a:hover{
	color: #a1a1a1;
	text-decoration: none;
}



.social-networks {
	display: flex;
	flex-wrap: wrap;
	text-align: center;
}

.social-networks a {
	width: 33%;
	font-size: 30px;
	color: #fff;
}


@media only screen and (max-width: 768px) {
	.footer > .footer-item {
		width: 100%;
	}
}






