.app-product-pagination {
	display: inline-block;
	margin-bottom: 5%;
	

}
.app-product-pagination div {
	color: #AF4C4C;
  	padding: 1% 3% 1% 3%;


  	
  	
  	float: left;
  	border: 1px solid #AF4C4C;
  	margin: 0 4px;
}
.app-product-pagination div.active {
  background-color: #4CAF50;
  color: white;
  border: 1px solid #4CAF50;
}
.app-product-pagination div:hover {
	background-color: #AF4C4C;
	color: white;

}