/* APP DISCOUNT TITLE AND HR LINE */

.app-discount-title {
	font-size: 30px;
	font-family: Montserrat;
	color: #AF4C4C;
}
.app-discount-hr {
	border: 2px solid #AF4C4C;
	background: #AF4C4C;
}

/* BASE DISCOUNT IMAGE  */

.app-discount-image-div {
	width: 100%;
	text-align: center;
}
.app-discount-image {
	width: 55%;
}


/*  BASE DISCOUNT INFO */

.app-discount-name {
	padding: 0 0 2% 0;
	font-size: 25px;
	font-family: Montserrat;
	color: black;
}
.app-discount-data {
	font-size: 18px;
	font-family: Roboto;
	color: black;
}
.app-discount-term {
	font-size: 16px;
	font-family: Roboto;
	color: black;
}
.app-discount-sub-image {
	width: 100%;
}
.app-discount-middle {
	padding: 1% 1% 1% 1%;
	border: 1px solid #AF4C4C;
	width: 60%;
	margin-left: 20%;
	cursor: pointer;
}
.app-discount-text {

	border: 1px solid border;
	background: #AF4C4C;
	color: white;
	font-family: Montserrat;
}	


