.menu-navbar {
	background-color: #287746;
}


.menu-navbar > .navbar-toggler {
	color: #fff;
	width: 100%;
}

.menu-navbar > .navbar-collapse > .navbar-nav {
	width: 100%;
}


.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item > .nav-link {
	color: #fff;
	text-align: center;
	/*border-radius: 0px !important;*/
}

.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item {
	color: #fff;
	text-align: center;
	width: 16.66%;
	

}

.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item > .dropdown-menu {
	background-color: #1c964b;
}

.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item > .dropdown-menu > li > .dropdown-item{
	color: #fff;
	text-align: center;
}
.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item > .dropdown-menu > li > .dropdown-item:hover{
	color: #fff;
	background-color: #6cc77f;
}

@media all and (min-width: 992px) {
	.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item > .dropdown-menu{ 
		display: none; 
	}
	.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item:hover > .nav-link{ 
		color: #fff;  
	}
	.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item:hover > .dropdown-menu{ 
		display: block; 
	}
	.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item > .dropdown-menu{ 
		margin-top:0; 
	}
}	


@media only screen and (max-width: 768px) {
	.cart-navbar > .cart > i > .cart-basket {
		right: 0px;
	}

	.about-us-navbar > .about-us-item {
		width: 32%;
	}

	.about-us-navbar > .about-us-item:nth-last-child(2), .about-us-navbar > .about-us-item:last-child {
		width: 50%;
	}

	.menu-navbar > .navbar-collapse > .navbar-nav > .nav-item {
		width: 100%;
	}

	.product-buttons > a {
		width: 80%;
		margin-bottom: 10px;
	}

	.product-buttons {
		justify-content: center;	
	}
	#logo {
		width: 40%;
	}

	.cart-logos a:nth-child(-n + 3){
		margin-right: 20px;
		font-size: 20px; 
	}

}