/**********  HeaderMid  ******************/

.app-mid-header {
	padding-left: 4%;
	padding-top: 1%;
	padding-bottom: 1%;
	background-color:#111111 !important;
}
.app-mid-header > .navbar-brand {
	font-family: 'Prociono', serif;

	font-size: 300% !important;
}
#app-base-logo {
	width: 25%;
}
.app-mid-header-logos a:nth-child(-n + 3){
	margin-right: 30px;
	font-size: 30px;
	color: #FAFAFA; 
}





.app-mid-header-logos a:last-child{
	color: #FAFAFA; 
}
.app-mid-header-cart-count {
	font-size: .9rem;
	position: absolute;
	top: -6px;
	right: 46px;
	width: 25px;
	height: 25px;
	color: #fff;
	background-color: #418deb;
	border-radius: 70%;
}
.app-mid-header-cart {
	font-size: 50px;
	padding-right: 5%;
	color: #FAFAFA;
	text-decoration: none;
}
