.app-order-input-title {
    font-size: 20px;
    margin-top: 3%;
    margin-bottom: 3%;
    font-family: Georgia, 'Times New Roman', Times, serif;
    
}
.app-order {
    margin-top: 5%;
    
}
.app-order-counter {
    background-color: #AF4C4C;
    height: 10vh;
    margin-top: 5%;
    margin-bottom: 3%;
    color: white;
    font-size: 100%;
}
.app-variant-order {
    margin-top: 10%;
    margin-bottom: 3%;
}
.app-data-order > .form-group {
    padding-top: 3%;
}
.app-variant-order > .form-group {
    padding-top: 3%;
}
.app-order-finish-button {
   
    background-color: #d87258;
    border: 1px solid white;
    color: white;
    height: 100%;
    width: 50%;
}
.app-order-finish-button:hover{
    background: #d8755d;
    color: white;
}
.app-order-summa{
    font-size: 120%;
    
}
@media only screen and (max-width: 768px) {
    .app-order-finish-button {
        height: 100%;
        width: 80%;
    }
    .app-order-summa{
        font-size: 80%;
        width: 70%;
    }
}