.about-us-navbar {
	text-align: center;
	padding-top: 8vh;
	padding-bottom: 4vh;
	background-color: #287746;
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.about-us-navbar > .about-us-item{
	width: 20%;
	text-align: center;
}
.about-us-navbar > .about-us-item > i{
	color: #fff;
	font-size: 50px;
}
.about-us-navbar > .about-us-item > p{
	color: #fff;
	font-size: 100%;
	padding: 10px;
	font-family: 'Prociono', serif;
}