/* APP TITLE AND HR LINE */

.app-delivery-title {
	font-size: 30px;
	font-family: Montserrat;
	color: #AF4C4C;
	margin-top: 2%;
}

.app-delivery-hr {
	border: 2px solid #AF4C4C;
	background: #AF4C4C;
	margin-top: 5%;
	margin-bottom: 5%;
}


/* APP DELIVERY INFO */
.app-delivery-info div {
	padding: 1% 0 1% 0;
}
.app-delivery-name {
	font-size: 24px;
	font-family: Roboto;
	color: #AF4C4C;
}
.app-delivery-term {
	font-size: 18px;
	font-family: Roboto;
	color: #AF4C4C;
}